/**
 * WordPress dependencies
 */
import domReady from '@wordpress/dom-ready'

domReady( () => {
	document.body.addEventListener( 'click', event => {
		const button = event.target.closest( '.ugb-blog-posts__load-more-button' )
		if ( ! button ) {
			return
		}

		const container = button.closest( '[data-id]' )
		if ( ! container ) {
			return
		}

		const id = container.getAttribute( 'data-id' )
		const num = container.getAttribute( 'data-load-items' )
		const page = ( parseInt( button.getAttribute( 'data-page' ), 10 ) || 1 ) + 1
		let totalPosts = 0

		button.classList.add( 'ugb-blog-posts--busy' )

		fetch( `${ window.stackable.restUrl }wp/v2/blog_posts_pagination?id=${ id }&page=${ page }&num=${ num }` )
			.then( response => {
				// Get the total number of available posts.
				totalPosts = parseInt( response.headers.get( 'X-UGB-Total-Posts' ), 10 )
				return response.json()
			 } )
			.then( posts => {
				const container = button.closest( '.ugb-blog-posts' ).querySelector( '.ugb-blog-posts__item' ).parentNode

				// Append the posts to the block.
				const temp = document.createElement( 'div' )
				temp.innerHTML = posts
				while ( temp.querySelector( '.ugb-blog-posts__item' ) ) {
					const el = temp.querySelector( '.ugb-blog-posts__item' )
					el.classList.add( 'ugb-blog-posts--hide' )
					container.insertBefore( el, container.querySelector( '.ugb-blog-posts__item:last-of-type' ).nextSibling )
					setTimeout( () => {
						el.classList.remove( 'ugb-blog-posts--hide' )
					}, 10 )
				}

				// Move to the next page.
				button.setAttribute( 'data-page', page )

				// Check if we don't have any more remaining posts to show.
				if ( container.querySelectorAll( '.ugb-blog-posts__item' ).length === totalPosts ) {
					container.querySelector( '.ugb-button-container' ).remove()
				}
			} )
			.finally( () => {
				if ( button ) {
					button.classList.remove( 'ugb-blog-posts--busy' )
				}
			} )

		event.preventDefault()
	} )
} )
